/* Reset & base */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #f9fafb;
  color: #1f2937;
  line-height: 1.6;
}

/* Header */
header {
  background: #111827;
  color: #fff;
  text-align: center;
  padding: 3rem 1rem;
}
header h1 {
  margin: 0;
  font-size: 2.8rem;
}
header p {
  margin: 0.5rem 0 1.5rem;
  font-size: 1.2rem;
  color: #9ca3af;
}
header nav a {
  color: #fff;
  margin: 0 1rem;
  text-decoration: none;
  font-weight: 600;
}
header nav a:hover {
  color: #38bdf8;
}

/* Sections */
section {
  max-width: 900px;
  margin: 3rem auto;
  padding: 0 1rem;
}
section h2 {
  font-size: 2rem;
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 0.5rem;
  margin-bottom: 1.5rem;
}

/* Projects */
.project {
  background: #fff;
  padding: 1.5rem;
  margin: 1.5rem 0;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.project:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 14px rgba(0,0,0,0.1);
}
.project h3 {
  margin-top: 0;
}
.project a {
  text-decoration: none;
  color: #1f2937;
}
.project a:hover {
  color: #2563eb;
}

/* Contact */
#contact p {
  margin: 0.5rem 0;
}
#contact a {
  color: #2563eb;
  font-weight: 600;
  text-decoration: none;
}
#contact a:hover {
  text-decoration: underline;
}

/* Footer */
footer {
  background: #111827;
  color: #9ca3af;
  text-align: center;
  padding: 1rem;
  margin-top: 2rem;
  font-size: 0.9rem;
}
